// animations

export function animationHandler(entity) {
 const enemyBox = entity.modal
  const animation = dom.basic_slash.cloneNode(true)
      animation.classList.remove("hidden")
  const animation_duration = animation.getAttribute("data-duration")

  enemyBox.append(animation)
  setTimeout(() => {
    animation.remove()
  }, animation_duration);
}

